.droit-card {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  border-radius: 15px;
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.15);
  background: rgba(253, 253, 253, 0.8);
  margin: 20px auto;
  width: 100%;
  max-width: 90vw;
  height: 60vh;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  flex-wrap: wrap;
  overflow: hidden;
  text-align: center;
}

.droit-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 20px rgba(0, 0, 0, 0.25);
}

.text-content {
  flex: 1;
  padding: 20px;
  box-sizing: border-box;
  max-height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.text-content h2 {
  font-size: 1.8em;
  margin-bottom: 0.5em;
}

.text-content p {
  font-size: 1em;
  line-height: 1.6;
  max-width: 90%;
}

.image-content {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-height: 100%;
  overflow: hidden;
}

.image-content img {
  width: 50%;
  height: auto;
  max-height: 100%;
  object-fit: contain;
  border-radius: 10px;
}

.image-right .image-content {
  order: 2;
}

.image-left .image-content {
  order: -1;
}

@media (max-width: 1024px) {
  .droit-card {
    flex-direction: column;
    height: auto;
    margin-top:0
  }

  .text-content, .image-content {
    width: 100%;
    max-height: auto;
    text-align: center;
  }

  .image-right .image-content, .image-left .image-content {
    order: 0;
  }

  .text-content h2 {
    font-size: 1.5em;
  }

  .text-content p {
    font-size: 1em;
  }
}

@media (max-width: 600px) {
  .droit-card {
    padding: 10px;
    width: 95vw;
  }

  .text-content {
    padding: 10px;
    width: 100%;
    text-align: center;
  }

  .text-content h2 {
    font-size: 1.2em;
  }

  .text-content p {
    font-size: 0.9em;
    line-height: 1.4;
  }

  .image-content img {
    width: 80%;
  }
}
