/* PageAction.css */

.nos-actions {
  gap: 20px;
  padding: 20px;
  box-sizing: border-box;
  background: linear-gradient(to right, red, yellow);
}

.nos-actions h2 {
  text-align: center;
  width: 100%;
}
section{
  margin: 20px;
  padding: 20px;
  background: rgba(253, 253, 253, 0.8);
}
#courrier-section {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  background: rgba(253, 253, 253, 0.8);
  border-radius: 15px;
}



.action-card img,
.tract-card img {
  height: 30%;
  border-radius: 10px;
}

.download-item {
  margin: 10px 0;
  width: 30%;
}

.download-link {
  background-color: #007bff;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  text-decoration: none;
  display: inline-block;
}

.download-link:hover {
  background-color: #0056b3;
}

@media (min-width: 768px) {
  .nos-actions {
    grid-template-columns: repeat(2, 1fr);
  }
  .action-card,
  .tract-card {
    flex-direction: row;
    text-align: left;
  }

  .action-card img,
  .tract-card img {
    width: 50%;
    max-width: 500px;
  }

}

@media (max-width: 768px) {
  section{
    margin: 0px;
    padding: 0px;
  }
  .nos-actions {
    grid-template-columns: repeat(3, 1fr);
  }

}
